import React from 'react';
import './Sobre.css';
import { ReactComponent as GrupoPesquisaSVG } from '../assets/O GRUPO DE PESQUISA.svg';
import { ReactComponent as Lupinha } from '../assets/lupinha.svg';
import { ReactComponent as Blocudos } from '../assets/blocudos.svg';

export const Sobre = () => {
  return (
    <div id='sobre' className="Sobre text-center">
      <GrupoPesquisaSVG className="mb-4" />
      <div className="content-wrapper">
        <div className="text-content">
          <p>Fundado em 2024, o grupo de pesquisa CoDes (Combate  Desinformao e ao Discurso de dio) tem como objetivo estudar, projetar e desenvolver estratgias para combater desinformao, fake news e discurso de dio. Utilizamos estratgias de Inteligncia Artificial, com foco em Aprendizagem de Mquina e Processamento de Linguagem Natural.</p>
          <p>Nosso trabalho pretende envolver a avaliao de diferentes mquinas de aprendizagem e a combinao de classificadores, alm da construo de bancos de dados em portugus e a gerao de dados sintticos. Para alm disso, tambm propor arquiteturas para lidar com esses desafios, discutindo questes cruciais como tica, IA e desinformao.</p>
        </div>
        <Lupinha className="lupinha mb-40" />
        <Blocudos className="blocudos mt-10" />
      </div>
    </div>
  );
};
